html{
  font-size: 10px;
}
body{
  background: #F2F2F2;
  
}

.card{
  padding:30px;
}
.shopStatus{
  .heading{
    font-size: 1.6rem;
    line-height: 19px;
  }
  i{
    font-size: 2rem;
    margin-right: 5px;
  }
  .data{
    font-size: 3.6rem;
    line-height: 44px;
  }  
}
.totalRevenue .data{color: #7ED321;}
.totalCost .data{color: #D0021B;}
.netIncome .data{color: #4A90E2;}


.siteItem{
  height:89px;
  td{
    vertical-align: middle;
  }
  .siteIcon{
    width:45px;
    text-align: center;
  }
  .siteCount,
  .siteRate{
    text-align: right;
  }
  .siteRate.add{
    color:green;
    &:before{
      font-family: "Font Awesome 5 Free"; 
      font-weight: 900; 
      content: "\f062";
      margin-right: 5px;
    }
  }
  .siteRate.minus{
    color:red;
    &:before{
      font-family: "Font Awesome 5 Free"; 
      font-weight: 900; 
      content: "\f063";
      margin-right: 5px;
    }
  }
}
.is-100x100{
  width:100px;
  height:100px;
}
.lstestOrderItem{
  position: relative;
  align-items:center;
  i{
    width:15px;
    text-align: center;
  }
  .name{
    font-size: 2rem;
    line-height: 24px;
    color:#000;
  }
  .dateTime{
    font-size: 1.6rem;
    line-height: 24px;
    color: #9B9B9B; 
  }
  .cat{
    font-size: 1.6rem;
    line-height: 24px;
    color: #9B9B9B; 
  }
  .total{
    position: absolute;
    right:0;
    bottom:0;
    text-align: right;
    font-size: 2rem;
    font-weight: bold;
    .label{
      font-size: 1.4rem;
    }
  }
}
